import React, { useState, useEffect } from 'react';
import {
  MdLocationOn,
  MdWork,
  MdEmail,
  MdSchool,
  MdKeyboardArrowLeft,
  MdKeyboardArrowRight,
  MdAccountBox,
  MdWebAsset,
  MdInfo,
} from 'react-icons/md';
import {
  FaWhatsapp,
  FaNodeJs,
  FaReact,
  FaHtml5,
  FaCss3Alt,
  FaJsSquare,
} from 'react-icons/fa';

import api from '~/services/api';
import {
  Content,
  LeftBar,
  AboutContainer,
  WorkContainer,
  WorkSelect,
  WorkImage,
  List,
  Item,
  ItemImage,
  ItemSkill,
} from './styles';

import GymPoint from '~/assets/GymPoint.png';

const Projects = [
  {
    name: 'GymPoint',
    img: GymPoint,
    link: 'https://github.com/igorjung/bootcamp-gostack-gymPoint',
  },
];

export default function HomePage() {
  const [avatar, setAvatar] = useState('');
  const [resize, setResize] = useState(false);
  const [index, setIndex] = useState(0);
  const [display, setDisplay] = useState(0);

  useEffect(() => {
    async function loadAvatar() {
      const { data } = await api.get('/users/igorjung');

      setAvatar(data.avatar_url);
    }

    const windowWidth = window.innerWidth;

    if (windowWidth <= 350) {
      setResize(true);
    }

    loadAvatar();
  }, []);

  return (
    <>
      <LeftBar>
        <nav>
          <button type="button" onClick={() => setDisplay(0)}>
            <MdAccountBox size="100%" color={!display ? '#000' : '#666'} />
          </button>
          <button type="button" onClick={() => setDisplay(1)}>
            <MdWebAsset size="100%" color={display ? '#000' : '#666'} />
          </button>
        </nav>
      </LeftBar>

      <Content>
        <WorkContainer visible={display === 1}>
          <WorkSelect>
            <button
              type="button"
              disabled={!Projects[index - 1]}
              onClick={() => setIndex(index - 1)}
            >
              <MdKeyboardArrowLeft size={30} color="#333" />
            </button>
            <a target="blank" href={Projects[index].link}>
              <h1>{Projects[index].name}</h1>
            </a>
            <button
              type="button"
              disabled={!Projects[index + 1]}
              onClick={() => setIndex(index + 1)}
            >
              <MdKeyboardArrowRight size={30} color="#333" />
            </button>
          </WorkSelect>
          <WorkImage>
            <div>
              <img src={Projects[index].img} alt={Projects[index].name} />
              <a target="blank" href={Projects[index].link}>
                <MdInfo size="100%" color="#fff" />
              </a>
            </div>
          </WorkImage>
        </WorkContainer>

        <AboutContainer visible={display === 0}>
          <div>
            <List>
              <ItemImage>
                <img src={avatar} alt="IgorJung" />
              </ItemImage>
              <Item>
                <MdLocationOn size={20} color={resize ? '#333' : '#ddd'} />
                <strong>Volta Redonda, Brazil</strong>
              </Item>
              <Item>
                <MdWork size={20} color={resize ? '#333' : '#ddd'} />
                <strong>Web developer</strong>
              </Item>
              <Item>
                <MdEmail size={20} color={resize ? '#333' : '#ddd'} />
                <strong>igor.jung@outlook.com</strong>
              </Item>
              <Item>
                <FaWhatsapp size={20} color={resize ? '#333' : '#ddd'} />
                <strong>+55 (24) 98848-2727</strong>
              </Item>
            </List>
          </div>
          <div>
            <h1>Education/ Certifications</h1>
            <List>
              <Item>
                <MdSchool size={20} color={resize ? '#333' : '#ddd'} />
                <div>
                  <h1>IFRJ - Instituto Federal do Rio de Janeiro</h1>
                  <strong>Certificate in industrial automation</strong>
                  <p>2016-2019</p>
                </div>
              </Item>
              <Item>
                <MdSchool size={20} color={resize ? '#333' : '#ddd'} />
                <div>
                  <h1>Rocketseat</h1>
                  <strong>ReactJs, React Native and NodeJs course</strong>
                  <p>2019</p>
                </div>
              </Item>
            </List>
            <h1>Skills</h1>
            <List>
              <Item>
                <ItemSkill>
                  <div>
                    <FaHtml5 size={20} color="#F16528" />
                    <p>HTML5</p>
                  </div>
                  <div>
                    <FaCss3Alt size={20} color="#56A3E7" />
                    <p>CSS3</p>
                  </div>
                  <div>
                    <FaJsSquare size={20} color="	#F7DF1D" />
                    <p>JavaScript</p>
                  </div>
                </ItemSkill>
              </Item>
              <Item>
                <ItemSkill>
                  <div>
                    <FaNodeJs size={20} color="#90C63D" />
                    <p>NodeJs</p>
                  </div>
                  <div>
                    <FaReact size={20} color="#5CDEF9" />
                    <p>ReactJs</p>
                  </div>
                  <div>
                    <FaReact size={20} color="#34AAC8" />
                    <p>ReactNative</p>
                  </div>
                </ItemSkill>
              </Item>
            </List>
          </div>
        </AboutContainer>
      </Content>
    </>
  );
}
